html,body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%; }
html {
    font-size: 1rem; }
.wrapper {
    position: relative;
    min-height: 100%;
    overflow: hidden; }
.container {
    margin: 0 auto;
    max-width: 1140px;
    padding: 0 0.3125rem; }
.body {
    min-width: 320px; }
h1,h2,h3,h4,h5,h6,p {
    margin: 0;
    padding: 0; }
.list--reset {
    list-style: none;
    margin: 0;
    padding: 0; }
@media screen and (min-width: '768px') {
    .container {
        padding: 0 1.25rem; } }
